.credit {
    z-index: 999;
    bottom: 0px;
    left: 50%;
    padding: 8px 12px;
    margin-top: 20px;
    font-family: "Poppins";
    font-size: 11px;
    color: rgb(248, 241, 241);
    background: rgba(25, 11, 11, 0.43);
    backdrop-filter: blur(10px);
    border-radius: 20px;
  }
.credit a {
    color: rgb(75, 70, 70) !important;
    font-weight: 700;
}